.footer {
    background-color: var(--White);
    padding: 100px 0;
    &__inner {
        padding: 0 290px;
        .flexCell {
            width: 33.3%;
        }
    }
    .title-h4 {
        margin-bottom: 7px;
        display: inline-block;
    }
    li {
        padding-top: 25px;
        color: var(--footerText);
        line-height: 1.2;
    }
    &__contacts {
        li {
            color: var(--contactsColor);
        }
        &-phone {
            a {
                white-space: nowrap;
                &:first-child {
                    &::after {
                        content: "|";
                        color: var(--lineColor);
                        margin: 0 5px 0 10px;
                        vertical-align: text-top;
                    }
                }
            }
        }
    }
}
ul.footer__contacts {
    margin-bottom: 35px;
}
@media(max-width: 1750px) {
    .footer {
        &__inner {
            padding: 0 200px;
        }
    }
}
@media(max-width: 1550px) {
    .footer {
        &__inner {
            padding: 0 100px;
        }
    }
}
@media(max-width: 1350px) {
    .footer {
        &__inner {
            padding: 0 20px;
        }
    }
}
@media(max-width: 1200px) {
    .footer {
        &__inner {
            padding: 0;
            margin: 0 -15px;
            justify-content: space-between;
            .flexCell {
                width: auto;
            }
        }
    }
}
@media(max-width: 900px) {
    .footer {
        padding: 60px 0;
        &__inner {
            flex-wrap: wrap;
            .flexCell {
                width: 100%;
                &:not(:last-child) {
                    margin-bottom: 45px;
                }
            }
        }
    }
}